.componentHeaderWrapper {
  display: flex;
  justify-content: space-between;
}

.cardWrapper {
  width: 100%;
  height: 200px;
  padding: 20px;
  background: #ffffff;

  box-shadow: 0px 3px 6px -4px rgba(126, 126, 126, 0.2),
    0px 6px 16px rgba(73, 73, 73, 0.08),
    0px 9px 28px 8px rgba(130, 130, 130, 0.05);
  border-radius: 3px;
}
.cardWrapperNoPadding {
  width: 100%;
  height: 200px;
  background: #ffffff;
  padding-top: 20px;
  padding-bottom: 20px;

  box-shadow: 0px 3px 6px -4px rgba(126, 126, 126, 0.2),
    0px 6px 16px rgba(73, 73, 73, 0.08),
    0px 9px 28px 8px rgba(130, 130, 130, 0.05);
  border-radius: 3px;
}

/* .cardWrapperNoPadding::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 0;
} */

.card {
  margin-bottom: 20px;
}
.cardTitle {
  margin-bottom: 20px;
}

.cardTitleNoPadding {
  margin-left: 20px;
  margin-bottom: 20px;
}

.cardTitle,
.cardTitleNoPadding {
  display: flex;
  gap: 4px;
}
