.mainWrapper {
  display: grid;
  grid-template-columns: repeat(24, 1fr);
}

.infoWrapper {
  margin-top: 260px;
  grid-column-start: 3;
  grid-column-end: 17;
}

.title {
  font-weight: 600;
  font-size: 40px;
  line-height: 125%;
  margin-bottom: 40px;
}

.description {
  font-family: "Golos UI";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.03em;
  margin-bottom: 60px;
}

.numsWrapper {
  margin-top: 273px;
  display: flex;
  gap: 40px;

  grid-column-end: 22;
}

@media screen and (max-width: 1350px) {
  .infoWrapper {
    margin-top: 120px;
    margin-bottom: 120px;
    grid-column-start: 3;
    grid-column-end: 22;
  }

  .title,
  .description {
    text-align: center;
  }

  .buttonWrapper {
    display: block;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
  }

  .numsWrapper {
    margin: 0;
    display: flex;
    gap: 40px;
    grid-column-end: 22;
    grid-column-start: 3;
    margin-left: auto;
    margin-right: auto;
  }
}

/* NUMBER ONE */

.numOneWrapper {
  position: relative;
  width: 140px;
  height: 280px;
}

.numOneElement1,
.numOneElement1Color {
  position: absolute;
  left: 0;
  top: 0;
  width: 40px;
  height: 140px;
  background-color: #0b68b5;
  border-radius: 3px 3px 0px 0px;
}
.numOneElement1 {
  animation-duration: 1200ms;
  animation-name: numOneElement1;
}

.numOneElement1Color {
  animation-duration: 4200ms;
  animation-name: numOneElement1Color;
  animation-iteration-count: infinite;
}

@keyframes numOneElement1 {
  from {
    left: -13px;
    top: -28px;
    transform: rotate(9.7deg);
  }

  to {
    left: 0;
    top: 0;
    transform: rotate(0);
  }
}

@keyframes numOneElement1Color {
  0% {
    background-color: #0b68b5;
  }
  50% {
    background-color: #4a95d2;
  }
  100% {
    background-color: #0b68b5;
  }
}

.numOneElement2,
.numOneElement2Color {
  position: absolute;

  width: 140px;
  height: 40px;
  background-color: #2077bf;
  border-radius: 0px 0px 0px 3px;
  top: 140px;
  left: 0;
}

.numOneElement2 {
  animation-duration: 1200ms;
  animation-name: numOneElement2;
}
.numOneElement2Color {
  animation-duration: 3500ms;
  animation-name: numOneElement2Color;
  animation-iteration-count: infinite;
}

@keyframes numOneElement2 {
  from {
    left: -7px;
    top: 127px;
    transform: rotate(9.7deg);
  }

  to {
    left: 0;
    top: 140px;
    transform: rotate(0);
  }
}

@keyframes numOneElement2Color {
  0% {
    background-color: #2077bf;
  }
  50% {
    background-color: #4a95d2;
  }
  100% {
    background-color: #2077bf;
  }
}

.numOneElement3,
.numOneElement3Color {
  position: absolute;
  right: 0;

  width: 40px;
  height: 140px;
  background-color: #4a95d2;
  border-radius: 3px 3px 0px 0px;
}

.numOneElement3 {
  animation-duration: 1200ms;
  animation-name: numOneElement3;
}

.numOneElement3Color {
  animation-duration: 5000ms;
  animation-name: numOneElement3Color;
  animation-iteration-count: infinite;
}

@keyframes numOneElement3 {
  from {
    right: 8px;
    top: -20px;
    transform: rotate(-6.7deg);
  }

  to {
    right: 0;
    top: 0;
    transform: rotate(0);
  }
}

@keyframes numOneElement3Color {
  0% {
    background-color: #4a95d2;
  }
  50% {
    background-color: #69abe0;
  }
  100% {
    background-color: #4a95d2;
  }
}

.numOneElement4,
.numOneElement4Color {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 40px;
  height: 140px;
  background-color: #69abe0;
  border-radius: 0px 0px 3px 3px;
}

.numOneElement4 {
  animation-duration: 1200ms;
  animation-name: numOneElement4;
}
.numOneElement4Color {
  animation-duration: 6500ms;
  animation-name: numOneElement4Color;
  animation-iteration-count: infinite;
}

@keyframes numOneElement4 {
  from {
    right: -8px;
    bottom: -20px;
