.clientLoginFormWrapper {
  margin-top: 16vh;
}

.clientLoginFormTitle {
  font-weight: 700;
  font-size: 40px;
  margin-bottom: 40px;
  text-align: center;
}

.clientLoginFormLabel {
  display: flex;
  flex-direction: column;
  padding-bottom: 8px;
  margin-bottom: 20px;
}

.placeholderSize {
  font-size: 16px;
}

.clientLoginButton {
  width: 100%;
  height: 40px;
}

.informRegLinkWrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-top: 32px;
}

.regButton {
  padding: 0;
}

@media (max-width:600px) {
  .clientLoginFormTitle {
   font-size: 32px;
   margin-bottom: 32px;
  }
}