@font-face {
  font-family: "Golos UI";
  src: url("../src/fonts/GolosUI-Regular.woff2") format("woff2"),
    url("../src/fonts/GolosUI-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Lato";
  src: url("../src/fonts/Lato-Regular.woff2") format("woff2"),
    url("../src/fonts/Lato-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Lato";
  src: url("../src/fonts/Lato-SemiBold.woff2") format("woff2"),
    url("../src/fonts/Lato-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Lato";
  src: url("../src/fonts/Lato-Bold.woff2") format("woff2"),
    url("../src/fonts/Lato-Bold.woff") format("woff");
  font-weight: Bold;
  font-style: normal;
  font-display: swap;
}

body {
  margin: 0;
  font-family: "Golos UI", sans-serif;
  letter-spacing: 0.05rem !important;
  font-weight: 400 !important;
}

button {
  margin: 0;
  font-family: "Golos UI", sans-serif;
  letter-spacing: 0.04rem !important;
  font-weight: 400 !important;
}

p {
  font-family: "Golos UI", sans-serif !important;
  letter-spacing: 0.04rem !important;
  font-weight: 400 !important;
}

a {
  font-family: "Golos UI", sans-serif;
  letter-spacing: 0.04rem !important;
  font-weight: 400 !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lato", sans-serif;
  font-weight: 700 !important;
  letter-spacing: 0.04rem !important;
  line-height: 2;
}

