.btn {
  padding: 0;
}

.columnWrapper {
  display: grid;
  grid-template-columns: repeat(22, 1fr);
}

.columnWrapper,
.columnWrapperSkeleton {
  gap: 20px;
}

.settingLabel {
  margin: 0;
  margin-bottom: 20px;

  padding: 0;
  grid-column-start: 1;
  grid-column-end: 4;
}

.settingDescription {
  margin: 0;
  margin-bottom: 20px;

  padding: 0;
  grid-column-start: 4;
  grid-column-end: 8;
}

.btnExit {
  position: fixed;
  bottom: 120px;
  left: 45%;
  width: 267px;
}
.transitionWrapper {
    width: 300px;
    height: 300px;
    animation: fadein 0.5s;
}
@keyframes fadein {
    from{opacity: 0;}
    to{opacity: 1;} 
  }