.eventCardWraper {
  display: flex;
  gap: 8px;
  width: 100%;
  border-bottom: solid 1px #f0f0f0;
  padding: 12px 0;
  transition: 0.5s;
}
.eventCardWraper:hover {
  cursor: pointer;
  background: #e9f2f9;
  transition: 0.2s;
  
}
.title {
  color: #1f1f1f;
  line-height: 19.2px;
}
.dotOn::before {
  content: "";
  position: absolute;
  display: inline-block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #0b68b5;
  left: -8px;
  top: 4px;
  transform: translateX(-50%);
}

.wrapperText {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  margin-left: 20px;
}

.text {
  font-size: 14px;
  color: #8c8c8c;
}
.wrapperTag {
  margin-left: auto;
  display: flex;
  flex-flow: column;
  align-items: end;
  justify-content: space-between;
  margin-right: 20px;
}
.tag {
  margin-bottom: 12px;
  margin-right: 0;
}
.data {
  text-align: right;
  font-size: 14px;
  color: #8c8c8c;
}
