.page {
  position: relative;
}

.pageHeaderWrappe {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  margin-left: 60px;
  margin-right: 60px;
}
.logo {
  width: 120px;
}

.logo img {
  width: 100%;
  height: auto;
}

.confirmWrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 30%);
  width: fit-content;
}

.pageTitle {
  text-align: center;
  font-weight: 700;
  font-size: 32px;
  margin-bottom: 40px;
}

.pageMainInfo {
  margin-bottom: 40px;
}

.pageSubTitle {
  margin-bottom: 20px;
}

.pageDdescription {
  margin-bottom: 60px;
}

.pageMainInfoHighLight {
  color: #000;
}

.confirm {
  width: 520px;
  height: fit-content;
  padding: 48px 40px 56px;
  border-radius: 3px;
  box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12),
    0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 40px;
}

.pageBtn {
  width: 100%;
  /* margin-bottom: 40px; */
}

.pageTimer {
  text-align: center;
}

.pageBtnNewPass {
  margin-bottom: 140px;
}

.formWrapper {
  margin-bottom: 40px;
}
