.registrLoginFormTitle {
    font-weight: 700;
    font-size: 40px;
    margin-bottom: 40px;
    text-align: center;
}

.registrFormWrapper {
    margin-top: 8vh;
    margin-bottom: 8vh;
}

.inputWrapper {
    margin-bottom: 20px;
}
.inputWrapperEnd {
    margin-bottom: 32px;
}

.buttonPosition {
    width: 100%;
    
  }

  .decriptionWrapper {
    display: flex;
    justify-content: space-between;
    padding-top:32px ;
    
  }

  .descriptionButton {
    padding:0;
  }

  .formValidChekWrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    transition: all 5s ease;
    opacity: 1;
    visibility: visible;
    margin-bottom: 20px;
  }
  .formValidItem {
    display: flex;
    align-items: center;
    width: 50%;
  }
  .validPoint {
    width: 10px;
    height: 10px;
    background: #595959;
    border-radius: 50px;
    margin-right: 12px;
  }
  .validPointOn {
    width: 10px;
    height: 10px;
    background: #69abe0;
    border-radius: 50px;
    margin-right: 12px;
  }

  .validContainer{
    display: flex;
  }

  @media (max-width:600px) {
    .registrLoginFormTitle {
     font-size: 32px;
     margin-bottom: 32px;
    }
  }
  
 