.tabWrapper {
  width: 100%;
  overflow: auto hidden;
  padding-left: 20px;
}

.tabTitleWrapper {
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  white-space: nowrap;
  align-items: center;
  list-style: none;
  gap: 20px;
  width: 100%;
  margin-bottom: 4px;
}
.tabNonActive {
  color: #595959;
  list-style-type: none;
  cursor: pointer;
  position: relative;
  transition: 0.1s ease-out;
  outline: none;
}

.tabNonActive:hover {
  color: #000000;
}

.tabActive {
  color: #000000;
  list-style-type: none;
  cursor: pointer;
  position: relative;
  transition: 0.1s ease-out;
}

.tabChapter {
  list-style-type: none;
  padding: 0.425em 0.75rem;
  border-radius: 0.2em;
}

.tabChapterNonActive {
  color: #595959;
  cursor: pointer;
  position: relative;
  transition: 0.1s ease-out;
  outline: none;
}

.tabChapterNonActive:hover {
  color: #2077bf;
}
.tabChapterNonActive:active {
  background-color: #dceef5;
  color: #2077bf;
}

.tabChapterActive {
  color: #fff;
  background-color: #2077bf;
  list-style-type: none;
  cursor: pointer;
  position: relative;
  transition: 0.1s ease-out;
}

.tabDisabled {
  color: rgba(0, 0, 0, 0.25);
}

.tabIndicator {
  position: relative;
  overflow: hidden;
  height: 4px;
  background: #2077bf;
  left: 0px;
  border-radius: 5px;
  transition: all 350ms ease-in-out;
}
