.loginWrapper {
  display: grid;
  grid-template-columns: repeat(24, 1fr);
  height: 100vh;
}

.titleInfoLogin {
  color: #fff;
  margin-top: 120px;
  line-height: 58px;
  font-weight: 600;
}
.logo,
img {
  width: 100%;
  height: auto;
}
.logo {
  width: 120px;
}


.loginImgArea {

  background-image: url(../img/Moscow-city.jpg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-color: #000;
}

.loginAreaContainer {
  margin: 20px 60px 0;
}

.headerButtons {
  width: 100%;
  text-align: end;
  padding: 20px 60px 0 0;
}
* + .headerButton {
  margin-left: 1rem;
}



@media (min-width:1921px) {

  .titleInfoLogin {
    font-size: 48px;
    
  }
  
  .loginImgArea {
    grid-column-start: 1;
    grid-column-end: 9;
    
  }
  
  .loginFormArea {
    grid-column-start: 9;
    grid-column-end: 25;
  }
  
  .formWrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
  }
  
  .formWrapperPosition {
    grid-column-start: 4;
    grid-column-end: 9;
    /* position: relative; */

    width: 460px;
    margin: 0 auto;
  }

  .logoMobile {
    display: none;
  }
  
}

@media (min-width:1200px) and (max-width:1920px) {
 
  .titleInfoLogin {
    color: #fff;
    margin-top: 120px;
    font-size: 40px;
    line-height: 58px;
  }
  
  .loginImgArea {
    grid-column-start: 1;
    grid-column-end: 12;
    
  }
  
  .loginFormArea {
    grid-column-start: 13;
    grid-column-end: 25;
  }
  
  .formWrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
  }
  
  .formWrapperPosition {
    grid-column-start: 2;
    grid-column-end: 11;
    width: 460px;
    margin: 0 auto;
  }

  .logoMobile {
    display: none;
  }

}

@media (min-width:751px) and (max-width:1199px){
 
  .titleInfoLogin {
    display: none;
  }
  
  .headerButtons {
    width: 100%;
    display: flex;
    padding: 20px 60px 0 60px;
    justify-content: space-between;
  }
  
  
  .loginImgArea {
  display: none;
  }
  
  .loginFormArea {
    grid-column-start: 1;
    grid-column-end: 25;
  }
  
  .formWrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
  }
  
  .formWrapperPosition {
    grid-column-start: 4;
    grid-column-end: 10;
    /* position: relative; */
  }
  
  .logoMobile,
  img {
    width: 100%;
    height: auto;
  }

  .logoMobile {
    width: 120px;
  }


}
@media (max-width:750px) {
  .titleInfoLogin {
    display: none;
  }
  
  .headerButtons {
    padding: 0;
    padding-top: 20px;
    display: grid;
    grid-template-columns: repeat(24,1fr);
  }
  
  
  .loginImgArea {
  display: none;
  }
  
  .loginFormArea {
    grid-column-start: 1;
    grid-column-end: 25;
  }
  
  .formWrapper {
    display: grid;
    grid-template-columns: repeat(24, 1fr);
  }
  
  .formWrapperPosition {
    grid-column-start: 2;
    grid-column-end: 24;
  }
  
  .logoMobile,
  img {
    width: 100%;
    height: auto;
  }

  .logoMobile {
    width: 80px;
    grid-column-start: 2;
    grid-column-end: 12;
  }

  .headerButtonWrapper {
    grid-column-start: 12;
    grid-column-end: 24;
  }
  .headerButton {
    height: 30px ;
  }

}


.page {
  position: relative;
}

.pageHeaderWrappe {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  margin-left: 60px;
  margin-right: 60px;
}
.logo {
  width: 120px;
}

.logo img {
  width: 100%;
  height: auto;
}

.confirmWrapper{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 30%);
  width: fit-content;

}

.pageTitle {
  text-align: center;
  font-weight: 700;
  font-size: 32px;
  margin-bottom: 40px;
}

.pageMainInfo {
  margin-bottom: 40px;

}

.pageSubTitle {
  margin-bottom: 20px;
}

.pageDdescription {
  margin-bottom: 60px;
}

.pageMainInfoHighLight {
  color: #000;
}

.confirm {
  width: 520px;
  height: fit-content;
  padding: 48px 40px 56px;
  border-radius: 3px;
  box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
  margin-bottom: 40px;
}

.pageBtn {
  width: 100%;
  margin-bottom: 40px;
}
.pageBtnRepeat {
  padding: 0;
}

.pageTimer {
  text-align: center;
}

.pageBtnNewPass {
  margin-bottom: 140px;
}

.formWrapper{
  margin-bottom: 40px;
}

.authwindowWrapper {
  margin: 0;
  padding: 0;
  align-items: center;
}