@keyframes usb {
  from {
    top: 20px;
  }
  /* 50% {top:10px} */
  to {
    top: -16px;
  }
}
.wrapper__animation-Login {
  width: fit-content;
  height: 500px;
  position: absolute;
  top: 36%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
.animation__login-title {
  font-family: "Lato";
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 38px;
  margin-bottom: 40px;
}
.animation__login-text {
  font-family: "Golos UI";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.03em;
  margin-bottom: 20px;
}
.wrapper__animation-usb {
  position: relative;
  width: 170px;
  height: 150px;
  margin-top: 20px;
  background: linear-gradient(to right, #0b68b5 2px, transparent 2px) 0 0,
    linear-gradient(to right, #0b68b5 2px, transparent 2px) 0 100%,
    linear-gradient(to left, #0b68b5 2px, transparent 2px) 100% 0,
    linear-gradient(to left, #0b68b5 2px, transparent 2px) 100% 100%,
    linear-gradient(to bottom, #0b68b5 2px, transparent 2px) 0 0,
    linear-gradient(to bottom, #0b68b5 2px, transparent 2px) 100% 0,
    linear-gradient(to top, #0b68b5 2px, transparent 2px) 0 100%,
    linear-gradient(to top, #0b68b5 2px, transparent 2px) 100% 100%;

  background-repeat: no-repeat;
  background-size: 40px 40px;
  border-radius: 2px;
  padding: 25px;
}
.animation__usb-port {
  /* z-index: 100000000; */
  /* overflow: hidden; */
  display: flex;
  justify-content: center;
}

.animation__usb-usb svg {
  position: absolute;
  /* top: 4px; */
  z-index: 1;
  animation: usb 1.5s infinite ease-in-out;
}
.animation__usb-usb {
  height: 100px;
  overflow: hidden;
  position: relative;
  width: 120px;
  display: flex;
  justify-content: center;
}
.wrapper__animation-usb-usb {
  position: absolute;
  top: 39px;
  height: 100px;
}

.wrapper__animation-arrow {
  overflow: hidden;
  width: 0;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  animation: anim 1s linear forwards;
}
@keyframes anim {
  50% {
    opacity: 0;
    width: 0;
  }
  100% {
    opacity: 1;
    width: 100%;
  }
}

.animation__reg-block {
  width: 54px;
  height: 12px;
  background: #2077bf;
  animation: 1s linear 0.5s infinite alternate move;
  margin-bottom: 12px;
}
@keyframes move {
  0% {
    opacity: 1;
    background: #69abe0;
    transform: translate(0, 0);
  }
  100% {
    opacity: 1;
    background: #2077bf;
    transform: translate(30px);
  }
}
.animation__reg-block--static {
  width: 54px;
  height: 12px;
  margin-bottom: 12px;
  margin-left: 31px;
  background: #2077bf;
}

.animation__usb-usb-need-help {
  margin-top: 115px;
  color: #2077bf;
}
