@import "~antd/dist/antd.less";

body {
  margin: 0;
  font-family: "Golos UI";
}

//SKELETON styles
.custom-skeleton__for-card.ant-skeleton.ant-skeleton-element.ant-skeleton-active {
  width: 100% !important;
}
//MENU Styles

.ant-menu-item-selected {
  color: #ffffff !important;
  background-color: #0b68b5 !important;
}

.ant-menu,
a {
  font-family: "Lato", sans-serif !important;
  letter-spacing: 3% !important;
}

.ant-menu.ant-menu-inline-collapsed > .ant-menu-item {
  left: 0;
  padding: 0 calc(50% - 20px / 2) !important;
  text-overflow: clip !important;
}

.ant-menu > .ant-menu-item-selected a,
.ant-menu > .ant-menu-submenu-selected a {
  color: #ffffff !important;
}

.ant-menu > .ant-menu-item-selected:hover {
  color: #ffffff !important;
}

// Steps Styles center position

.ant-steps-item-icon {
  position: relative !important;
}

.ant-steps-icon {
  position: absolute !important;
  top: 50% !important;
  left: 50% !important;
  margin-right: -50% !important;
  transform: translate(-50%, -50%) !important;
}

//FORM

.ant-form-item-label {
  font-family: "Lato", sans-serif !important;
  font-weight: 600;
}

.formModifiedLabel > .ant-form-item-row .ant-form-item-label > label {
  width: 100%;
  max-width: none;
}
// POPOVER Styles

.notificationPopOverITO
  > .ant-popover-content
  > .ant-popover-inner
  > .ant-popover-title {
  padding: 0 !important;
  padding-top: 20px !important;
  margin: 0 0 20px 20px !important;
  border-bottom: none;
}
.notificationPopOverITO
  > .ant-popover-content
  > .ant-popover-inner
  > .ant-popover-inner-content {
  padding: 0 !important;
}

.notificationPopOverITO > .ant-popover-content {
  width: 500px !important;
  height: 800px !important;
}

.notificationPopOverITO > .ant-popover-content::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

.ant-table-body::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

//     ,  

.uploadButtonPass > .ant-form-item-control > .ant-form-item-control-input {
  min-height: 0 !important;
}
