.cardCustom {
  width: "100%";
  margin-bottom: 40px;
  margin-top: 96px;
}



.cardMenuWrapper {
  display: flex;

  height: 64px;
}

.cardContentWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  margin-left: 16px;
}

.cardHeader {
  font-family: "Lato";
  font-weight: 600;
  font-size: 1rem;
  line-height: 100%;
  margin: 0;
}

.cardDescription {
  font-size: 0.75rem;
  margin: 0;
  color: #555555;
}

.cardDescriptionHeader {
  font-size: 0.75rem;
  margin: 0;
  color: #000000;
}
