.agentLoginFormWrapper {
    margin-top: 16vh;
  }
  
  .agentLoginFormTitle {
    font-weight: 700;
    font-size: 40px;
    margin-bottom: 40px;
    text-align: center;
  }
  
  .formWrapper {
    margin-bottom: 12px;

  }
  .buttonPosition {
    padding: 0;
    width: 100%;
    text-align: end;
    margin-bottom: 20px;
  }
  .inputWrapper {
    height: 40px;
  }
  
  .placeholderSize {
    font-size: 16px;
  }
  
  .agentLoginButton {
    width: 100%;
    height: 40px;
  }
  
  .informRegLinkWrapper {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-top: 32px;
  }
  
  .regButton {
    padding: 0;
  }

  .buttonLinkRegistr{
    padding: 0;
  }

  .descriptionWrapper {
    display: flex;
    justify-content: space-between;
    align-items: baseline;

  }
  

  @media (max-width:600px) {
    .agentLoginFormTitle {
     font-size: 32px;
     margin-bottom: 32px;
    }
  }