.columnWrapper {
  display: flex;
  flex-wrap: wrap;
  max-height: 200px;
  flex-direction: column;
  /* column-width: 400px; */
  width: 100%;
}

.columnItem {
  width: 50%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* width: fit-content; */
  /* column-gap: 32px; */
  margin-bottom: 4px;
}

.mainTitleWrapper {
  display: flex;
  justify-content: space-between;
}

.columnItemTitle {
  font-family: "Golos UI", sans-serif;
  letter-spacing: 0.05rem !important;

  /* grid-column-end: 1; */
  font-weight: 400 !important;
  line-height: 150%;
  color: #8c8c8c;
}

.columnItemTitle span {
  word-break: break-all;
}

.wrapperWithButton {
  display: flex;
  justify-content: space-between;
}

.mainTitle {
  font-size: 16px;
  font-weight: 600;
  color: #000000;
  margin-bottom: 20px;
}

.columnItemContent {
  line-height: 150%;
  /* width: 220px; */
  /* word-break: break-all; */
}

.iconButton {
  height: fit-content;
  width: fit-content;
  padding: 0;
  line-height: 100%;
  font-size: 24;
  color: #000000;
}
