.lineWrapper {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}
.formItemWrapper {
  margin-bottom: 0;
}
.lineInputSixCol {
  width: 48%;
  flex-grow: 6;
}

.lineInputFourCol {
  width: 23%;
  flex-grow: 4;
}

.lineInputThreeCol {
  width: 32%;
  flex-grow: 3;
}

.lineInputTwoCol {
  width: 48%;
  flex-grow: 2;
}

.lineInputOneCol {
  width: 100%;
  flex-grow: 1;
}

.lineInputCheckboxes {
  padding-top: 30px;
}

.btnWrapper {
  display: flex;
  justify-content: end;
  gap: 0.75rem;
}

.dividers {
  margin-bottom: 32px !important;
}

.btn {
  padding-left: 3rem;
  padding-right: 3rem;
  margin-top: 60px;
}
