.confirm {
  width: 520px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 48px 40px 56px;
  border-radius: 3px;
  box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12),
    0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
  margin: 160px auto 40px auto;
}

.pageTitle {
  text-align: center;
  font-weight: 700;
  font-size: 32px;
  margin-bottom: 40px;
}

.pageLink {
  padding: 0;
  margin-top: 20px;
}
