.wrapperModal {
  height: 589px;
  padding: 80px;
}
.wrapperModalTitle {
  margin-bottom: 62px;
}
.modalTitle {
  text-align: center;
}
.modalFzBlock {
  width: 239px;
  height: 196px;
  padding: 20px 28px;
  box-shadow: 0px 5px 6px -2px rgba(0, 0, 0, 0.12),
    0px 8px 18px rgba(0, 0, 0, 0.08), 0px 11px 30px 10px rgba(0, 0, 0, 0.05);
}
.modalFzIcon {
  margin-bottom: 12px;
}
.modalFzIcon svg {
  color: #0b68b5;
  width: 26px;
  height: 32.5px;
}
.modalFzTitle {
  margin-bottom: 24px;
}
.modalFzText {
  font-size: 0.75rem;
  color: #8c8c8c;
}
